.invite {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  background: white;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 3rem;
  height: 100%;
}

.header {
  display: flex;
  flex-direction: column;
  letter-spacing: 10px;
  font-size: 6rem;
  line-height: 95px;
  font-weight: 500;
  white-space: nowrap;
  font-family: "Great Vibes", cursive;
  text-shadow: 15px 15px 1px rgba(224, 168, 168, 0.1);
  color: #d4a373;
}

.text {
  font-size: 1.1rem;
  width: 80%;
  line-height: 1.6;
  color: #555;
}

.date {
  font-size: 2.1rem;
  font-family: "Great Vibes", cursive;

  color: #d4a373;
}

.time {
  font-size: 2.3rem;
  font-family: "Great Vibes", cursive;
  color: #d4a373;
}

.calendar {
  background: #fcf8f5;
  padding: 1rem;
  border-radius: 8px;
  width: 380px;
  margin-bottom: 10px;
  border-radius: 10%;
}

.weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin-bottom: 0.3rem;
  font-weight: 600;
}

.days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.3rem;
}

.day,
.emptyDay {
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.day {
  position: relative;
  font-size: 1rem;
  &:hover {
    background: #f1f3f5;
  }
}

.emptyDay {
  visibility: hidden;
}

.activeDay {
  color: #d4a373;
  font-weight: bold;

  .heart {
    position: absolute;
    width: 2.5rem;
    height: 2.5rem;
    animation: pulse 1.5s infinite;
  }

  .dayNumber {
    position: relative;
    z-index: 1;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.halfBorder {
  height: 1px;
  width: 90%;
  margin: 50px auto;
  background: linear-gradient(to right, transparent, #1f1a1a, transparent);
  opacity: 0.6;
  border-radius: 50%;
}
@media (max-width: 1920px) {
  .invite {
    gap: 10px;
  }
  .text {
    width: 60%;
    font-size: 1.5rem;
  }
  .date,
  .time {
    font-size: 2.5rem;
  }
}
@media (max-width: 1440px) {
  .header {
    font-size: 5rem;
    line-height: 85px;
  }

  .text {
    width: 60%;
    font-size: 1.3rem;
  }

  .date,
  .time {
    font-size: 2.4rem;
  }

  .calendar {
    width: 320px;
  }

  .day {
    font-size: 1.2rem;
  }
}

@media (max-width: 1024px) {
  .text {
    width: 70%;
    font-size: 1.4rem;
  }

  .date,
  .time {
    font-size: 2.4rem;
  }

  .calendar {
    width: 320px;
  }

  .day {
    font-size: 1.1rem;
  }
}

@media (max-width: 768px) {
  .invite {
    gap: 10px;
  }
  .header {
    font-size: 4.5rem;
    line-height: 70px;
  }

  .text {
    font-size: 1.4rem;
  }

  .date,
  .time {
    font-size: 2.4rem;
  }

  .calendar {
    width: 300px;
  }
  .day {
    font-size: 1.2rem;
  }
}

@media (max-width: 451px) {
  .invite {
    gap: 5px;
    margin-top: 10px;
  }
  .header {
    line-height: 50px;
    font-size: 4rem;
    margin: 20px 0 5px;
    white-space: normal;
    word-break: break-word;
  }

  .text {
    font-size: 1.4rem;
    width: 80%;
    text-align: center;
  }

  .date,
  .time {
    font-size: 2.6rem;
  }

  .calendar {
    width: 300px;
  }

  .day {
    font-size: 1.3rem;
  }
}

@media (max-width: 376px) {
  .invite {
    gap: 10px;
  }
  .header {
    font-size: 4rem;
    // margin: 20px 0 0px;
    white-space: normal;
    word-break: break-word;
  }

  .text {
    font-size: 1.5rem;
    width: 90%;
    text-align: center;
  }

  .date,
  .time {
    font-size: 2.8rem;
  }

  .calendar {
    width: 300px;
  }
  .day {
    font-size: 1.5rem;
  }
}

@media (max-width: 323px) {
  .invite {
    margin: 0;
  }
  .header {
    font-size: 5.4rem;
    line-height: 50px;
    margin: 35px 0 0px;
  }

  .text {
    font-size: 1.5rem;
    width: 90%;
    text-align: center;
  }

  .date,
  .time {
    font-size: 3.2rem;
  }

  .calendar {
    width: 270px;
  }
  .day {
    font-size: 1.6rem;
  }
}
